<template>
    <div class="tianjia">
        <div class="tianjia-top">
            
        </div>
        <div class="tianjia-container">
            <div class="tianjia-box1">
                <p></p>
                <p></p>
                <el-input v-model="form.questions_stem" placeholder=""></el-input>
            </div>
            <div class="tianjia-box2">
                <p></p>
                <div class="mackdownone">
                        <mark-down @on-save="save" ref="md" @on-change="changeImg"
                        :theme="theme" :initialValue="form.title" 
                        :placeholderValue="placeholderValue"/>
                </div>
                
            </div>
            <div class="tianjia-box3">
                <p></p>
                 <el-select v-model="value1" placeholder="" @change="fn1">
                    <el-option
                    v-for="(item,index) in list"
                    :key="index"
                    :label="item.exam_name"
                    :value="item.exam_id">
                    </el-option>
                </el-select>
                <p></p>
                 <el-select v-model="value2" placeholder="" @change="fn2">
                    <el-option
                    v-for="(item,index) in newlist"
                    :key="index"
                    :label="item.subject_text"
                    :value="item.subject_id">
                    </el-option>
                </el-select>
                <p></p>
                 <el-select v-model="value3" placeholder=""  @change="fn3">
                    <el-option
                    v-for="(item,index) in listone"
                    :key="index"
                    :label="item.questions_type_text"
                    :value="item.questions_type_id"
                   
                    >
                    </el-option>
                </el-select>
                <div class="mackdowntwo">
                        <mark-down @on-save="save1" ref="md1" @on-change="changeImg"
                        :theme="theme" :initialValue="form.questions_answer" 
                        :placeholderValue="placeholderValue"/>
                </div>
                 <el-button type="primary" @click="sub"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
import Axios from "axios";
import MarkDown from 'vue-meditors'

export default {
    components:{
        MarkDown
    },
    methods:{
        save(content){
            // console.log(":"+content.value);
            // console.log(":"+content.html);
            // console.log("theme:"+content.theme);
            this.form.title = content.value;
        },
        save1(content){
            //  console.log(":"+content.value);
             this.form.questions_answer = content.value;
        },
        
        changeImg(files,callback){
                // console.log(files)
                //
            var url = data.url;
                callback(url);//callback
        },
          getlist(){
            Axios.get('/exam/examType').then(res=>{
                this.list = res.data.data;
            })
              Axios.get('/exam/subject').then(res=>{
                //   console.log(res)
                this.newlist = res.data.data;
            })
            Axios.get('/exam/getQuestionsType').then(res=>{
              this.listone = res.data.data;
            })
        },
        fn1(item){
            this.form.exam_id = item;
        },
        fn2(item){
            this.form.subject_id = item;
        },
        fn3(item){
            this.form.questions_type_id = item;
        },
        sub(){
            
         this.$refs.md.handleSave();
         this.$refs.md1.handleSave();
            Axios.post('/exam/questions',this.form).then(res=>{
                // console.log(res);
                if(res.data.code==1){
                    this.$Message.success('');
                    this.$router.push('/home/chakanshiti');
                }
                
            })
        // console.log(this.form)
        }
        

    },
    data(){
        return{
             placeholderValue:"",//placeholder
            initialValue: "",//markdown
            initialValue1:"",
            theme: 'OneDark',//
            list: [],
            newlist:[],
            listone:[],
            value1:"",
            value2:"",
            value3:"",
            form:{
                questions_type_id:"",
                questions_stem:"",
                subject_id:"",
                exam_id:"",
                user_id:localStorage.getItem("user_id"),
                questions_answer:"",
                title:"",
            }
        }
    },
    created(){
        this.getlist();
    }
}
</script>

<style lang="scss">
    .tianjia{
        width: 100%;
        height: 100%;
        background: #eee;
        display: flex;
        flex-direction: column;
        overflow: auto;
        .tianjia-top{
            width: 95%;
            height: 70px;
            margin: 0 auto;
            line-height: 70px;
            font-size: 20px;
        }
        .tianjia-container{
            width: 95%;
            margin: 0 auto;
            background: #fff;
            display: flex;
            flex-direction: column;
            margin-bottom: 30px;
            padding-left: 25px;
            .tianjia-box1{
                p{
                    margin-top: 15px;
                }
            }
            .tianjia-box2{
                p{
                    margin-top: 15px;
                }
            }
            .tianjia-box3{
                p{
                    margin-top: 30px;
                    margin-bottom: 15px;
                }
                .mackdowntwo{
                    margin-bottom: 50px;
                }
            }
        }
    }
</style>